import React, {useState, useEffect} from 'react';
import{useDispatch, useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import './login.css';
import {RegisterUser} from '../store/Actions/UserActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';

function Register(props) {

    const dispatch = useDispatch();
    const [Name, setName]= useState('');
    const [Email, setemail]= useState('');
    const [Password, setpassword] = useState('');
    const [ConfirmPassword, setconfirmpassword] = useState('');

    const redirect = (props.location.search) ? 
    (props.location.search.split("=")[1])
    : ('/');

    const Register = useSelector(state=> state.Register);
    const {UserInfo, loading, error}= Register;

    const OnSubmitHandler=(e)=>{
        e.preventDefault();
        if(Password !== ConfirmPassword){
            alert('Password Does Not Matched');
        }else{
            dispatch(RegisterUser(Name, Email, Password)); 
        }
    }

    useEffect(()=>{
        if(UserInfo){
            props.history.push(redirect);
        }
    },[props.history, redirect, UserInfo]);



    return (
        <div className='main-login-container'>
            <div className='login-container'>

            <div className='login-left-container'>
            <h4 className='mb-3' style={{fontWeight: '600', textTransform: 'uppercase', letterSpacing: '0.5px', color: '#37475A'}}>Create A New SNMART Account</h4>
            <div style={{display: 'flex', flexDirection: 'column', gap: '10px'}}>
                <h5 style={{fontWeight: '500',color: '#37475A'}}>Account Benefits</h5>
                <p style={{fontSize: '13px', textAlign: 'justify'}}>If you create an account with us, you will get additional benefits such as order history, bonus cash and more.</p>
                <button className='btn-login'><Link to='/register' style={{color: 'white'}}>Create an account</Link></button>
            </div>
            </div>

            <div className='login-right-container'>
            <form onSubmit={OnSubmitHandler}>
            {loading && (<LoadingBox></LoadingBox>)}
                       {error && (<MessageBox>{error}</MessageBox>)}
            <h4 className='mb-3' style={{fontWeight: '600', color: '#37475A'}}>Create your free account</h4>
            <label className='lbl-login'>Name</label>
            <input type='text' 
            className='txt-login mb-3'
            onChange={(e)=> setName(e.target.value)}/>
            <label className='lbl-login'>Email Address</label>
            <input 
            type='text' 
            className='txt-login mb-3'
            onChange={(e)=> setemail(e.target.value)}/>
            <label className='lbl-login'>Password</label>
            <input 
            type='password' 
            className='txt-login mb-3'
            onChange={(e)=>setpassword(e.target.value)}/>
            <label className='lbl-login'>Confirm Password</label>
            <input 
            type='password'
            className='txt-login mb-3'
            onChange={(e)=>setconfirmpassword(e.target.value)}/>
            <button type='submit' className='btn-login mt-2 mb-3'>Create Account</button>
            </form>
            </div> 
                


    
            </div>         
        </div>
    )
}

export default Register;
