import React, {useEffect, useState} from 'react';
import './PlaceOrder.css';
import {useDispatch, useSelector} from 'react-redux';
import formatCurrency from '../Currency';
import {Link} from 'react-router-dom';
import { DetailsOrder, PayOrder } from '../store/Actions/PlaceOrderActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';
import Axios from 'axios';
import {PayPalButton} from 'react-paypal-button-v2';
import { ORDER_PAY_RESET } from '../store/Constants/PlaceOrderConstants';

function OrderDetailsView(props) {

    const orderId = props.match.params.id;
    const OrderDetails = useSelector((state)=>state.OrderDetails);
    const {order, loading, error} = OrderDetails;

    const [sdkReady, setsdkReady] = useState(false);

    const OrderPay = useSelector((state)=> state.OrderPay);
    const {loading: loadingpay, 
        error: errorpay, success: successpay} = OrderPay;

    const dispatch = useDispatch();

    useEffect(()=>{
        const AddPayPalScript = async()=>{
            const {data} = await Axios.get('/api/config/paypal');
            const script = document.createElement("script");
            script.type = "text/javascript";
            script.src = `https://www.paypal.com/sdk/js?client-id=${data}`;
            script.async = true;
            script.onload=()=>{
            setsdkReady(true);
            };
            document.body.appendChild(script);
        };

        if(!order || successpay || (order && order._id !== orderId)){
            dispatch({type: ORDER_PAY_RESET});
            dispatch(DetailsOrder(orderId));
        }else{
            if(!order.IsPaid){
                if(!window.paypal){
                    AddPayPalScript();
                }else{
                    setsdkReady(true);
                }
            }
        }
    }, [dispatch, order,orderId, sdkReady]);


    const SuccessPaymentHandler=(PaymentResults)=>{
        dispatch(PayOrder(order, PaymentResults));
    }

    return (
        (loading)?(<LoadingBox></LoadingBox>):
        (error)? (<MessageBox>{error}</MessageBox>)
        :
        (<div className='main-placeorder-container'>
           <div className='order-id-container'><span>Order : {order._id}</span></div>
           <div className='container-fluid d-flex flex-wrap justify-content-center mt-3'>

            <div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Shipping</h5>
                <div className="card-body order-address">
                 <ul><li><strong>Name : </strong>{order.ShippingAddress.FullName}</li>
                 <li><strong>Address : </strong>{order.ShippingAddress.Address}</li>
                 <li><strong>City : </strong>{order.ShippingAddress.City}</li>
                 <li><strong>Postal Code : </strong>{order.ShippingAddress.PostalCode}</li>
                 <li><strong>Country : </strong>{order.ShippingAddress.Country}</li></ul>
                 <div>{(order.IsDelivered)?(<MessageBox>Delivered at {order.IsDelivered}</MessageBox>)
                : (<MessageBox>Not Delivered</MessageBox>)}</div>
                 </div>
                </div>
               </div>
             </div>

            <div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Payments</h5>
                <div className="card-body order-payment">
                 <ul><li><strong>Method : </strong>{order.PaymentMethod}</li></ul>
                 <div>
                {(order.IsPaid)?(<MessageBox>Paid at {order.PaidAt}</MessageBox>)
                : (<MessageBox>Not Paid</MessageBox>)}
                </div>
                 </div>
               </div>
             </div>
            </div>

            <div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Ordered Items</h5>
                <div className="card-body">
                {order.OrderItems.map((item)=>(
                        <div className='container mb-3 cart-items'>
                        <div><img className='place-order-image' key={item._id} src={item.Image}/></div>
                        <div><Link to={`/product/${item.product}`}><strong>{item.Title}</strong></Link></div>
                        <div><strong>
                        {item.qty} X {formatCurrency(item.Price)} = {formatCurrency((item.qty)*(item.Price))}
                        </strong></div>
                        </div>
                    ))}
                 </div>
                </div>
               </div>
             </div>

             <div className="row">
             <div>{loading && <LoadingBox></LoadingBox>}
               {error && <MessageBox>{error}</MessageBox>}</div>
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Order Summary</h5>
                <div className="card-body summary">
                 <ul>
                        <li><strong>Items : </strong>{formatCurrency(order.ItemsPrice)}</li>
                        <li><strong>Shipping :</strong> {formatCurrency(order.ShippingPrice)}</li>
                        <li><strong>Taxes : </strong>{formatCurrency(order.TaxPrice)}</li>
                        <li><strong>Order Total : {formatCurrency(order.TotalPrice)}</strong></li>
                    </ul>
                    {(!order.IsPaid) && (
                        <div>
                            {(!sdkReady) ? (<LoadingBox></LoadingBox>):
                                (
                                <>
                                {errorpay && (<MessageBox>{errorpay}</MessageBox>)}
                                {loadingpay && (<LoadingBox></LoadingBox>)}
                                <PayPalButton amount={order.TotalPrice}
                                onSuccess={SuccessPaymentHandler}></PayPalButton>
                                </>)}</div>
                            )} 
                 </div>
                </div>
               </div>
             </div>
            </div>
    </div>)

        

    )
}

export default OrderDetailsView;
