import React, {useState, useEffect} from 'react';
import{useDispatch, useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import './login.css';
import {GoogleLogin} from 'react-google-login';
import {FcGoogle} from 'react-icons/fc';
import {SignIn} from '../store/Actions/UserActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';

function Login(props) {

    const dispatch = useDispatch();
    const [Email, setemail]= useState('');
    const [Password, setpassword] = useState('');

    const redirect = (props.location.search) ? 
    (props.location.search.split("=")[1])
    : ('/');

    const UserSign = useSelector(state=> state.UserSign);
    const {UserInfo, loading, error}= UserSign;

 

    const OnSubmitHandler=(e)=>{
        e.preventDefault();
        dispatch(SignIn(Email,Password));
    }

    useEffect(()=>{
        if(UserInfo){
            props.history.push(redirect);
        }
    },[props.history, redirect, UserInfo]);

    return (
        <div className='main-login-container'>
            <div className='login-container'>

            <div className='login-left-container'>
            <h4 className='mb-3' style={{fontWeight: '600',letterSpacing: '0.5px', color: '#37475A'}}>Sign In to SNMART</h4>
            <div style={{display: 'flex', flexDirection: 'column', gap: '10px'}}>
                <h5 style={{fontWeight: '500',color: '#37475A'}}>New Customer ?</h5>
                <p style={{fontSize: '13px', textAlign: 'justify'}}>If you create an account with us, you will get additional benefits such as order history, bonus cash and more.</p>
                <button className='btn-login'><Link to='/register' style={{color: 'white'}}>Create an account</Link></button>
            </div>
            <div style={{display: 'flex', marginTop: '1rem', flexDirection: 'column', gap: '10px'}}>
            <h5 style={{fontWeight: '500',color: '#37475A'}}>Social Logins</h5>
            <GoogleLogin
            clientId="GOOGLE_ID"
            render={(renderProps) => (
                <button
                className='btn-google'
                onClick={renderProps.onClick} 
                disabled={renderProps.disabled}>
                <FcGoogle/>{" "}Signed in with Google
                </button>
              )}/>
            </div>
            </div>


            
            <div className='login-right-container'>
            <form onSubmit={OnSubmitHandler}>
            {loading && (<LoadingBox></LoadingBox>)}
            {error && (<MessageBox>{error}</MessageBox>)}
            <h4 className='mb-3' style={{fontWeight: '600', color: '#37475A'}}>Sign In</h4>
            <label className='lbl-login'>Username / Email</label>
            <input 
            type='text' 
            className='txt-login mb-3'
            onChange={(e)=> setemail(e.target.value)}/>
            <label className='lbl-login'>Password</label>
            <input 
            type='password' 
            className='txt-login mb-3'
            onChange={(e)=>setpassword(e.target.value)}/>
            <button type='submit' className='btn-login mt-2 mb-3'>Login</button> 
            <span style={{fontSize: '15px'}} className='mt-4'>
                <Link className='login-links' to='/register'>Forgot password ?</Link>
            </span>
            </form>
            </div> 
                
            </div>        
        </div>
    )
}

export default Login;
