import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {Alert} from 'react-bootstrap';
import './Products.css';
import formatCurrency from '../Currency';
import { useDispatch , useSelector } from 'react-redux';
import { DetailsProduct } from '../store/Actions/ProductActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';
import {TiShoppingCart} from 'react-icons/ti';
import {Link} from 'react-router-dom';
import { Button } from '@material-ui/core';

function ProductScreen(props) {
    
    const dispatch = useDispatch();
    const [qty, setqty]= useState(1);
    const ProductID = props.match.params.id;
    const ProductDetails = useSelector((state)=> state.ProductDetails);
    const {loading, error, product} = ProductDetails;

    useEffect(()=>{
        dispatch(DetailsProduct(ProductID));
    } , [dispatch, ProductID]);


    const AddToCartHandler=()=>{
        props.history.push(`/cart/${ProductID}?qty=${qty}`);

    }

    return (
        <div>
            { loading ? (<LoadingBox></LoadingBox>)
            : error ? (<MessageBox>{error}</MessageBox>)
         : (<div className='product-container'>
            <div  className='single-product-container'>
            <div><img className='single-product-image' src={product.Image} alt={product.Title}/></div>

             <div className='single-product-details-container'>
                 <div><h2><Link className='single-product-screen-title' to={`/product/${product._id}`}>{product.Title}</Link></h2></div>
                 <div><p className='single-product-description'>{product.Description}</p></div>
                 <div><span className='single-product-screen-price'>{formatCurrency(product.Price)}</span></div>
                 <div style={{display: 'flex', gap: '0.5rem', alignItems: 'center'}}>
                     {"Available Sizes"}{" : "}{product.AvailableSize.map((size)=>(
                  <Button size='large' style={{borderRadius: '10rem', fontSize:'15px'}} variant='outlined'>{size}</Button>
                ))}
                 </div>
                 
             </div>

             <div className='add-cart-container'>
             <div style={{textAlign: 'center', fontSize: '20px'}}>
                {product.CountInStock > 0 ? 
                (<Alert variant='success'>In Stock</Alert>) 
                : (<Alert variant='danger'>Out of Stock</Alert>)}
                 </div>
                 
                 {(product.CountInStock > 0)
                 &&
                (<div style={{display: 'flex', gap: '1rem', flexDirection: 'column'}}>
                    <div><span style={{fontSize: '16px', letterSpacing: '1px'}}>Quantity{" : "}</span>
                        <select className='single-product-view-quantity'
                            value={qty} onChange={(e)=> setqty(e.target.value)}>
                            {[...Array(product.CountInStock).keys()].map(
                            (x)=>(<option key={x+1} value={x+1}>{x+1}</option>))}
                        </select>  
                    </div>
                    <div><button onClick={AddToCartHandler} className='add-cart-btn'><TiShoppingCart style={{fontSize: '1.5rem'}}/><span>Add to cart</span></button></div>
                </div>)}
             </div>
                 
             </div>

         </div>)}
        </div>
        
    )
}

export default ProductScreen;
