import React from 'react';
import {Link} from 'react-router-dom';
import './Products.css';
import {Button} from '@material-ui/core';
import formatCurrency from '../Currency';
import { Alert } from 'react-bootstrap';

export default function Products (props){
    const { product } = props;

   

        return (
            <Link to={`/product/${product._id}`} style={{textDecoration: 'none'}}>
            <div className='single-card' key={product._id}>
              <div> <img className='product-image' src={product.Image} alt={product.Title}/></div>
              <div>
                <Link className='product-title'>
                  {product.Title.substr(0,25)}{"..."}</Link></div>
              <div style={{display: 'flex', gap: '0.5rem'}}>
                {product.AvailableSize.map((size)=>(
                  <Button size='small' style={{borderRadius: '10rem', fontSize:'14px'}} variant='outlined'>{size}</Button>
                ))}
              </div>
              
              <div className='d-flex justify-content-between align-items-center'>
                <span className='product-price'>{formatCurrency(product.Price)}</span>
                <button className='btn-view-product'>
                <Link style={{textDecoration: 'none', color: 'white'}} to={`/product/${product._id}`}>View</Link>
                </button>
              </div>

              <div>
                {product.CountInStock > 0 ? 
                (<Alert variant='success'>In Stock</Alert>) 
                : (<Alert variant='danger'>Out Of Stock</Alert>)}
              </div>
            </div>
            </Link>
        )
    
}