import React, {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import { AddToCart, RemoveFromCart} from '../store/Actions/CartActions';
import {Link} from 'react-router-dom';
import formatCurrency from '../Currency';
import {CgCloseO} from 'react-icons/cg';
import {Alert} from 'react-bootstrap';
import './CartScreen.css';

function CartScreen(props) {
    const dispatch = useDispatch();
    const ProductID = props.match.params.id;
    const qty = props.location.search ? 
    Number(props.location.search.split("=")[1]) : 1;

    useEffect(()=>{
        if(ProductID){
            dispatch(AddToCart(ProductID, qty));
        }
    }, [dispatch, ProductID, qty]);

    const RemoveFromCartHandler =(id)=>{
        dispatch(RemoveFromCart(id));

    }

    const CheckOutHandler=()=>{
        props.history.push('/login?redirect=shipping')
    }

    const Cart = useSelector(state=> state.Cart);
    const{CartItems}= Cart;

    return (
        <div className='main-cart-container'>
           <div className='main-cart'>
           <div>{(CartItems.length === 0) ? (
                    <div className='empty-cart-container'>
                        <img src='/Images/cartempty.png' />
                        <span className='empty-cart-status'>Your Cart is Empty</span>
                            <div className='empty-cart-btn-container'>
                                <button><Link style={{color: '#37475A'}} to='/login'>Sign in to your account</Link></button>
                                <button><Link style={{color: '#37475A'}} to='/register'>Sign up now</Link></button>
                            </div>
                    </div>
                    
                ):
                (<div className='cart-container'>
                    {CartItems.map((item)=>(
                            <div className='d-flex justify-content-between align-items-center'>
                                <div className='cart-list-container' key={item._id}>
                                <img style={{width: '5rem'}} src={item.Image}/>
                                <h6><Link style={{textDecoration: 'none', color: '#70A401'}} to={`/product/${item.product}`}>{item.Title.substr(0,10)}</Link></h6>
                                <h6>{formatCurrency(item.Price)}</h6>
                                <div>
                                <select className='cart-list-product-quantity'
                                        value={item.qty} 
                                         onChange={(e)=>
                                            dispatch(AddToCart(item.product, Number(e.target.value))
                                          )}>
                                          {[...Array(item.CountInStock).keys()].map(
                                        (x)=>(<option key={x+1} value={x+1}>{x+1}</option>))}

                                       </select>
                                </div>
                            </div>

                            <div>
                                <CgCloseO className='delete-item-icon' 
                                onClick={()=>RemoveFromCartHandler(item.product)}/>
                            </div>

                            </div>
                        ))}
                </div>)}
            </div>
            

            <div className='cart-total-container'>
                <div>
                    <Alert variant='info'><h5 style={{fontWeight: '600', color: '#1B262C', letterSpacing: '1px'}}>
                    Sub Total :({CartItems.reduce((a,c)=> a + c.qty, 0)} items) : {" "} 
                    {formatCurrency(CartItems.reduce((a,c)=> a + c.Price * c.qty, 0))}</h5></Alert>
                <div className='cart-btn-container' style={{display: 'flex', gap: '1rem', paddingTop: '1rem'}}>
                <button className='cart-btn' style={{backgroundColor: 'gray'}}>
                    <Link style={{textDecoration: 'none',  color: 'white'}} to='/'>Keep Shopping</Link></button>
                <button className='cart-btn' onClick={CheckOutHandler} style={{backgroundColor: '#70A401'}}>
                Proceed To Checkout</button>
                </div>
                </div>
               
            </div>
           </div>

            
        </div>
    )
}

export default CartScreen;
